<script>
import { TimelineLite } from "gsap";
import { Howl } from 'howler';
import EventBus from '../../common/eventBus.js';
import { Events, SceneNames } from '../../common/constants';
import { mapState } from 'vuex'

export default {
    name: "BattleFireAttackScene",  
    data() {
        return {
            attackSound: null,
            showAttackResult: false,
            attackTimeLine: null,
        }
    },
    computed: {
        ...mapState({
            turnType: state => state.battle.turnType,
            isAttackHit: state => state.battle.isAttackHit
        })
    },
    mounted() {
        EventBus.$on(Events.Confirmation, this.onConfirmation);
        this.playNaviAnimation();  
    },
    beforeDestroy() {
        EventBus.$off(Events.Confirmation);
    },
    sockets: {
        changeScene() {
            this.moveToDamageScene();
        }
    },    
    methods: {
        playNaviAnimation() {
            this.attackTimeLine = new TimelineLite();

            this.attackTimeLine.to('#navi-attack-group', {y: 10, scaleX: 1.3, scaleY: 1.3, duration: 0.5, ease: "none"});
            this.attackTimeLine.to('#navi-attack-group', {y: 17, scaleX: 1.5, scaleY: 1.5, duration: 0.5, ease: "none"});
            this.attackTimeLine.set('#attack1', {display: 'block'});
            this.attackTimeLine.to('#navi-attack-group', {x: -15, duration: 0.5, ease: "none"});
            this.attackTimeLine.set('#attack1', {display: 'none'});
            this.attackTimeLine.set('#attack2', {display: 'block'});
            this.attackTimeLine.to('#navi-attack-group', {x: -27, duration: 0.5, ease: "none"});

            this.attackTimeLine.play()
            .then(() => this.playAttackAnimation());
            
            setTimeout(() => {
                if(this.$store.state.session.sound) {
                    this.attackSound = new Howl({
                        src: require("../../assets/sounds/attack.mp3"),
                        volume: 0.1,
                        loop: true
                    });

                    this.attackSound.play();
                }
            }, 100);

        },
        playAttackAnimation() {
            this.attackTimeLine = new TimelineLite({
                repeat: 7,
            });

            this.attackTimeLine.set('#navi-attack-group', {display: 'none'});
            this.attackTimeLine.to('#attacking_sprite1', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#attacking_sprite1', {display: 'none'});
            this.attackTimeLine.to('#attacking_sprite2', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#attacking_sprite2', {display: 'none'});
            this.attackTimeLine.to('#attacking_sprite3', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#attacking_sprite3', {display: 'none'});

            this.attackTimeLine.play().then(() => this.playExplosionAnimation());
        },
        playExplosionAnimation() {
            this.attackTimeLine = new TimelineLite();

            this.attackTimeLine.set('#attacking-group', {display: 'none'});
            this.attackTimeLine.to('#explosion_sprite1', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#explosion_sprite1', {display: 'none'});
            this.attackTimeLine.to('#explosion_sprite11', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#explosion_sprite11', {display: 'none'});            
            this.attackTimeLine.to('#explosion_sprite2', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#explosion_sprite2', {display: 'none'});
            this.attackTimeLine.to('#explosion_sprite3', {display: 'block', duration: 0.15,  ease: "none"});
            this.attackTimeLine.set('#explosion_sprite3', {display: 'none'});


            this.attackTimeLine.play().then(() => this.showResult());            
        },
        showResult() {
            if(this.attackSound !== null) {
                this.attackSound.stop();
            }

            var soundSrc = this.isAttackHit ? require("../../assets/sounds/hit-attack.mp3")
                : require("../../assets/sounds/miss-attack.mp3");

            var sound = new Howl({
                src: soundSrc,
                volume: 0.1,
                loop: true
            });

            sound.play();
            this.showAttackResult = true;

            setTimeout(() => {
                sound.stop();
                this.moveToDamageScene();
            }, 1500);

        },
        onConfirmation() {
            this.$socket.client.emit('changeSceneRequest', this.$store.state.battle.id);
        },
        moveToDamageScene() {
            if(this.attackSound !== null || this.attackTimeLine !== null) {
                this.attackSound.stop();
                this.attackTimeLine.kill();
            }
            this.$store.commit('session/setCurrentScene', SceneNames.BattleAttackDamage);
        }
    }
}
</script>

<style scoped lang="scss">
.cls-1,.cls-4{fill:#fff;}
.cls-2,.cls-3,.cls-4{font-size:24px;font-family:Advanced-PET-Font, Advanced PET Font;}
.cls-3{fill:none;stroke:#000;stroke-width:5px;}

#navi-attack-group {
    transform-origin: center;
    transform: ro;

    #attack1, #attack2 {
        display: none;
    }
}

#attacking-group {
    #attacking_sprite1, #attacking_sprite2, #attacking_sprite3 {
        display: none;
    }
}

#explosion-group {
    #explosion_sprite1, #explosion_sprite11, #explosion_sprite2, #explosion_sprite3 {
        display: none;
    }
}

.flip{ 
